<template>
    <el-dialog :title="this.title"
               @close="shelfClose"
               width="70%"
               :visible.sync="dialogVisible">
        <el-table :data="tableData"
                  :header-cell-style="{background:'#0096DC',color:'#fff'}"
                  :row-class-name="tableRowClassName"
                  border
        >
            <!--  No	库位	SAP No	品名	危险品标识	存放量	详细信息	年存量（暂定）-->
            <el-table-column type="index" label="No" width="50"></el-table-column>
            <el-table-column property="storagebin" label="库位" width="100"></el-table-column>
            <el-table-column property="materialno" label="SAP No" width="100"></el-table-column>
            <el-table-column property="description" label="品名"  width="200"></el-table-column>
            <el-table-column property="totalquantity" label="存放量"></el-table-column>
            <el-table-column property="baseunit" label="单位"></el-table-column>
            <el-table-column property="chemicalBrand" label="危险品标识" min-width="100">
                <template slot-scope="scope">
                    <el-image
                            style="width: 30px; height: 30px; margin-left: 5px;"
                            v-for="(img, index) in scope.row.imgArr"
                            :key="index"
                            :src="`./static/image/danger/${img}.jpg`">
                    </el-image>
                </template>
            </el-table-column>
            <el-table-column property="information" label="详细信息" min-width="80">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            type="primary"
                            @click="informationClick(scope.row.dOCLink)">查看</el-button>
                </template>
            </el-table-column>
        </el-table>
    </el-dialog>
</template>

<script>
    import {dangerMap} from "../utils/comObject";

    export default {
        data() {
            return {
                tableData: [],
                dialogVisible: false,
                title: ''
            }
        },
        props: {
            shelfDialogVisible: Boolean,
            selectShelf: Array
        },
        methods: {
            shelfClose() {
                this.$emit('closeShelfDialog');
            },
            tableRowClassName({row, rowIndex}) {
                if (rowIndex%2==0) {
                    return 'shelf-odd-row';
                } else {
                    return 'shelf-even-row';
                }
            },
            //详细信息
            informationClick(url) {
                window.open(url);
            }
        },
        watch: {
            shelfDialogVisible(newVal) {
                this.dialogVisible = newVal;
            },
            selectShelf(newVal) {
                this.title = newVal[0].storagebin;
                let obj = [];
                newVal.forEach(item => {
                    let chemicalBrand = item.chemicalBrand;
                    let imgArr = [];
                    if(chemicalBrand) {
                        let arr = chemicalBrand.split(';');
                        arr.forEach(brand => {
                            let img = dangerMap.get(brand);
                            if(!img) img = "blank";
                            imgArr.push(img);
                        })
                    }
                    obj.push({
                        ...item,
                        imgArr
                    })
                });
                this.tableData = obj;
            }
        },
        mounted() {
            this.dialogVisible = this.shelfDialogVisible;
        },
        created() {
        }
    }
</script>

<style lang="scss">
    .shelf-odd-row {
        background: #CBDDF2 !important;
    }
    .shelf-even-row {
        background: #E7EFF9 !important;
    }

</style>


