/*
* 墙、地板  模型
* */
import * as THREE from 'three'
let STORAGE_WIDTH, STORAGE_HEIGHT;
export function initFloor(scene, STORAGE_WIDTH, STORAGE_HEIGHT) {
    let loader = new THREE.TextureLoader();
    loader.load("./static/image/floor.jpg", function (texture) {
        texture.wrapS = texture.wrapT = THREE.RepeatWrapping;
        texture.repeat.set( 10, 10 );
        let floorGeometry = new THREE.BoxGeometry(STORAGE_WIDTH, 1, STORAGE_HEIGHT);
        let floorMaterial = new THREE.MeshBasicMaterial( { map: texture, side: THREE.DoubleSide } );
        let floor = new THREE.Mesh(floorGeometry, floorMaterial);
        floor.position.y = -0.5;
        floor.name = "floor";
        scene.add(floor);
    })
}



