/*
*   物料  盒子  模型
* */

import * as THREE from "three";
import {PLANE_GROUND,RULE_PLANE_WIDTH, PUSH_PLANE_WIDTH, PLANE_LENGTH, BOX_SIZE} from "./config";
import {dangerMap} from "../../utils/comObject";

//添加物料
export function materialModule(scene, shelfDataMap, materialDataMap) {
    materialDataMap.forEach(materials => {
        let material = materials[0];
        //文字模型
        let fontMesh = scene.getObjectByName(('font-' + material.storagebin));

        if(fontMesh == null) return
        //面板模型
        let planeMesh = fontMesh.parent;
        //单个货架模型
        let shelfMesh = planeMesh.parent;
        //整排货架模型
        let parentMesh = shelfMesh.parent;
        let planeWidth = getPlaneWidth(material.storagebin, shelfDataMap);
        let angle = parentMesh.rotation.y;
        let posX=0, posY=0, posZ=0;
        if(-Math.PI/2 == angle){
            posX = -PLANE_LENGTH/2;
            posZ = fontMesh.position.x-6 + shelfMesh.position.x + planeWidth/2;
        } else if(0 == angle) {
            posX = fontMesh.position.x-6 + shelfMesh.position.x + planeWidth/2;
            posZ = PLANE_LENGTH/2;
        } else if(Math.PI/2 == angle) {
            posX = PLANE_LENGTH/2;
            posZ = -(fontMesh.position.x-6 + shelfMesh.position.x) - planeWidth/2;
        } else {
            posX = -(fontMesh.position.x-6 + shelfMesh.position.x) - planeWidth/2;
            posZ = -PLANE_LENGTH/2;
        }
        if(fontMesh.rotation.y !== 0) {
            posZ = posZ + planeWidth - 6;
        }

        posX += parentMesh.position.x;
        posY = planeMesh.position.y + BOX_SIZE/2 + PLANE_GROUND;
        posZ += parentMesh.position.z - 2;

        addBox(scene, material, posX, posY, posZ, angle);
    });
}
//物料盒子
function addBox(scene, material, posX, posY, posZ) {
    let img = 'blank'
    let {chemicalBrand, storagebin} = material;
    if(chemicalBrand) {
        let arr = chemicalBrand.split(';');
        img = dangerMap.get(arr[0]);
        if(!img) img = "blank";
    }
    new THREE.TextureLoader().load(`./static/image/danger/${img}.jpg`, function (texture) {
        let geo = new THREE.BoxGeometry(BOX_SIZE,BOX_SIZE, BOX_SIZE);
        let mat = new THREE.MeshBasicMaterial( { map: texture, color: '#fff' } );
        let mesh = new THREE.Mesh(geo, mat);
        mesh.position.set(posX, posY, posZ);
        mesh.name = 'shelf-' + storagebin;
        scene.add(mesh);
    })
}

//获取面板长度
function getPlaneWidth(storagebin, shelfDataMap) {
    let lastIndex = storagebin.lastIndexOf('-');
    let shelf = shelfDataMap.get(storagebin.substring(0,lastIndex));
    let {shelftype} = shelf;
    if(shelftype === 1) {
        return RULE_PLANE_WIDTH;
    } else {
        return PUSH_PLANE_WIDTH;
    }
}


