<template>
    <el-container>
        <el-header height="80px">
            <img :src="logoUrl" class="logo-img">
            <span class="header-title">奥特斯化学品仓库可视化管理</span>
            <el-tooltip class="item" effect="dark" content="返回" placement="left">
                <i class="el-icon-right" @click="setting"></i>
            </el-tooltip>
            <HeaderMenu class="header-menu"></HeaderMenu>
        </el-header>
        <el-container class="container-wrap">
            <el-main class="main-wrap">
                <router-view></router-view>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
    import HeaderMenu from '../components/HeaderMenu'
    export default {
        data() {
            return {
                logoUrl: require('../assets/image/logo.jpg')
            }
        },
        methods: {
            setting() {
                this.$router.push('/');
            }
        },
        components: {
            HeaderMenu
        },
        created() {
        }
    }
</script>

<style lang="scss">
    html,body,#app{
        height: 100%;
    }
    .el-container {
        /*height: 100vh;*/
        height: calc(100vh - 80px);
        .grid-content {
            font-size: 16px;
            font-weight: bolder;
            color: #409EFF;
            /*border-bottom: 1px solid rgb(220, 223, 230);*/
            margin:0 0 20px 10px;
            padding-bottom: 10px;
        }
        .container-wrap {
            background-color: #F5F6F7;
            padding: 30px;
            /*.main-wrap {
                background-color: #fff;
            }*/
        }
    }
    .el-header {
        display: flex;
        align-items: center;
        //background-color: #0082df;
        box-shadow: 0px 3px 5px #888888;
        .logo-img {
            width: 80px;
            height: 40px;
        }
        .header-title {
            color: #093867;
            font: 24px '微软雅黑';
            font-weight: bolder;
            margin-left: 50px;
        }
        .header-menu {
            position: absolute;
            right: 200px;
        }
    }
    .el-aside::-webkit-scrollbar {
        display:none;
    }
    .el-aside {
        box-shadow: 0px 3px 5px #888888;
    }
    // 图标
    .el-icon-right {
        color: #000000;
        font-size: 40px;
        position: absolute;
        top: 20px;
        right: 40px;
        z-index: 20;
        cursor: pointer;
    }



</style>


