<template>
    <el-menu default-active="/shelfSetting"
             class="el-menu-demo"
             mode="horizontal"
             active-text-color="#409EFF"
             text-color="#8FC6FD"
             @select="handleSelect">
        <el-menu-item index="/shelfSetting" >货架管理</el-menu-item>
        <el-menu-item index="/storageSetting" >仓库基本信息</el-menu-item>
    </el-menu>
</template>

<script>
    export default {
        methods: {
            handleSelect(index) {
                if(this.$route.path === index) return;
                this.$router.push(index);
            }
        }
    }
</script>

<style scoped>

</style>


