<template>
    <div v-loading="loading"
         element-loading-text="拼命加载中"
    >
        <div class="shelf-3d" id="shelf-canvas">
        </div>
        <!--<el-tooltip class="item" effect="dark" content="返回" placement="left">
            <i class="el-icon-right" @click="setting"></i>
        </el-tooltip>-->
        <StorageDetailCard :storageId="storageId"
                            v-on:materialArrShow="materialArrShow"
        ></StorageDetailCard>
        <GoodsDialog :shelfDialogVisible="shelfDialogVisible"
                    :selectShelf="selectShelf"
                     @closeShelfDialog="closeShelfDialog"
            ></GoodsDialog>
    </div>

</template>

<script>
    import * as THREE from 'three'
    import { OrbitControls } from "three/examples/jsm/controls/OrbitControls"
    import {initFloor} from "../../assets/js/floorModule";
    import {initShelf} from "../../assets/js/shelfModule";

    import GoodsDialog from "../../components/GoodsDialog";
    import StorageDetailCard from "../../components/StorageDetailCard";
    import {shelfList, materialList} from "../../utils/actions";
    import {storageAreaMap} from '../../utils/comObject'

    export default {
        data() {
            return {
                scene: null,
                camera: null,
                light: null,
                renderer: null,
                controls: null,
                raycaster: null,
                mouse: null,
                animationId: null,
                clock: null,

                storageId: "",
                STORAGE_WIDTH: 0,   //仓库宽
                STORAGE_HEIGHT: 0,   //仓库长
                //货架详细信息是否显示
                shelfDialogVisible: false,
                selectShelf: null,

                shelfDataMap: new Map(),    //货架map
                materialMap: new Map(),     //物料map
                materialArr: [],            //物料list

                loading: true,
            }
        },
        components: {
            GoodsDialog, StorageDetailCard
        },
        methods: {
            initScene() {
                this.scene = new THREE.Scene();
                /*const axesHelper = new THREE.AxesHelper( 100);
                this.scene.add( axesHelper );*/
            },

            initCamera() {
                this.camera = new THREE.PerspectiveCamera(45, window.innerWidth/window.innerHeight, 1, 10000);
                // camera.position.set(STORAGE_WIDTH, STORAGE_WIDTH+STORAGE_HEIGHT, STORAGE_HEIGHT*2);
                this.camera.position.set(0, 1000, 0);
                this.camera.up.set(0,1,0);
                this.camera.lookAt(new THREE.Vector3(0, 0, 0));
            },

            initLight() {
                let dirLight = new THREE.DirectionalLight(0xffffff, 0.3);
                dirLight.color.setHSL(0.1, 1, 0.95);
                dirLight.position.set( 0, 200, 0).normalize();
                this.scene.add(dirLight);

                let ambLight = new THREE.AmbientLight(0xffffff, 1);
                ambLight.position.set(0, 0, 0);
                this.scene.add(ambLight);
            },

            initRender() {
                this.renderer = new THREE.WebGLRenderer({
                    antialias: true,
                });
                this.renderer.setSize(window.innerWidth, window.innerHeight);
                this.renderer.setClearColor("#ffffff", 1.0);
                document.getElementById("shelf-canvas").appendChild(this.renderer.domElement);
            },

            initObj() {
                initFloor(this.scene, this.STORAGE_WIDTH, this.STORAGE_HEIGHT);
                initShelf(this.scene, this.storageId, this.shelfDataMap, this.materialMap);
            },

            // 初始化轨迹球控件
            initControls() {
                this.controls = new OrbitControls( this.camera, this.renderer.domElement );
                this.controls.enableDamping = true;
                this.controls.dampingFactor = 0.5;
                // 视角最小距离
                this.controls.minDistance = 0;
                // 视角最远距离
                this.controls.maxDistance = 5000;
                // 最大角度
                this.controls.maxPolarAngle = Math.PI/2.2;
                //鼠标滚轮缩放
                // this.controls.enableZoom = false;
            },

            //点击
            onMouseClick( event ) {
                // 将鼠标位置归一化为设备坐标。x 和 y 方向的取值范围是 (-1 to +1)
                this.mouse.x = ( event.clientX / window.innerWidth ) * 2 - 1;
                this.mouse.y = - ( event.clientY / window.innerHeight ) * 2 + 1;
                this.clickHandle();
            },
            //触屏
            onTouchStart(event) {
                if (event.touches.length === 1) {
                    let touch2 = event.touches[0];
                    this.mouse.x = (touch2.pageX / window.innerWidth) * 2 - 1;
                    this.mouse.y = -(touch2.pageY / window.innerHeight) * 2 + 1;
                    this.clickHandle();
                }
            },

            clickHandle() {
                // 通过摄像机和鼠标位置更新射线
                this.raycaster.setFromCamera( this.mouse, this.camera );
                // 计算物体和射线的焦点
                const intersects = this.raycaster.intersectObjects( this.scene.children );
                for ( let i = 0; i < intersects.length; i ++ ) {
                    let name = intersects[ i ].object.name;
                    if(name.indexOf("shelf") != -1) {
                        this.shelfDialogVisible = true;
                        let bin = name.substring(name.indexOf("-")+1, name.length);
                        this.selectShelf = this.materialMap.get(bin);
                        break;
                    }
                }
            },
            
            //关闭shelf dialog
            closeShelfDialog() {
                this.shelfDialogVisible = false;
            },

            animate() {
                this.animationId = requestAnimationFrame(this.animate);
                this.renderer.render(this.scene, this.camera);
                this.update();
            },
            // 更新控件
            update() {
                this.controls.update();
            },

            init() {
                this.initRender();
                this.initCamera();
                this.initScene();
                this.initControls();
                this.initLight();
                this.initObj();
                this.renderer.clear();
                this.renderer.render(this.scene, this.camera);
            },

            backStorage() {
                this.$router.push('/storageMap');
            },

            //  获取 仓库货架信息
            getShelfDataMap(storageId){
                shelfList({storageId}).then(response => {
                    let shelfList = response.data;
                    shelfList.forEach(shelf => {
                        this.shelfDataMap.set(shelf.shelfbin, shelf);
                    })
                }).catch(e => {

                })
            },
            //获取 物料 数组
            getMaterialMap(storageId) {
                materialList({storageBin:storageId}).then(response => {
                    let map = this.materialMap;
                    let arr;
                    let materialList = response.data.list;
                    this.materialArr = materialList;
                    materialList.forEach(shelf => {
                        if(map.has(shelf.storagebin)) {
                            arr = map.get(shelf.storagebin);
                        } else {
                            arr = [];
                        }
                        arr.push(shelf)
                        map.set(shelf.storagebin, arr);
                    })
                }).catch(e => {

                })
            },

            //显示所有物料信息
            materialArrShow() {
                this.shelfDialogVisible = true;
                this.selectShelf = this.materialArr;
            }
        },
        mounted() {
            //点击事件
            this.raycaster = new THREE.Raycaster();
            this.mouse = new THREE.Vector2();
            this.clock = new THREE.Clock();
            this.init();
            this.animate();
            document.addEventListener( 'click', this.onMouseClick, false );
            document.addEventListener('touchstart', this.onTouchStart, false);
            setTimeout(() => this.loading = false, 3000)

            window.onresize = () => {
                return (() => {
                    this.camera.aspect = window.innerWidth / window.innerHeight;
                    this.camera.updateProjectionMatrix();
                    this.renderer.setSize(window.innerWidth, window.innerHeight);
                })();

            };

        },
        created() {
            this.storageId = this.$route.query.storageId;
            const storageMap = storageAreaMap.get(this.storageId);
            this.STORAGE_WIDTH = storageMap.storageWidth;
            this.STORAGE_HEIGHT = storageMap.storageHeight;

            this.getShelfDataMap(this.storageId);
            this.getMaterialMap(this.storageId);
        },
        beforeDestroy() {
            window.cancelAnimationFrame(this.animationId );
            document.removeEventListener("click", this.onMouseClick);
            document.removeEventListener("touchstart", this.onTouchStart);
        }
    }
</script>

<style scoped lang="scss">
    .shelf-3d {
        width: 100vw;
        height: 100vh;
    }

    // 图标
    /*.el-icon-right {
        color: #000000;
        font-size: 40px;
        position: absolute;
        top: 20px;
        right: 40px;
        z-index: 20;
        cursor: pointer;
    }*/

</style>


