<template>
    <el-row :gutter="25">
        <el-col :span="8"
                v-for="storage in storageArr"
                :key="storage.storageid"
                style="margin-bottom: 25px">
            <el-card>
                <div slot="header">
                    <span style="color: #1989fa;font-weight: bolder;">{{storage.storageid}}</span>
                    <el-button style="float: right; padding: 3px 0"
                               @click="handleSave(storage)"
                               type="text">保存</el-button>
                </div>
                <div>
                    <el-form>
                        <el-form-item label="仓库名称 " prop="storagename">
                            <el-input
                                    style="width: 70%"
                                    v-model="storage.storagename"
                                    placeholder="请输入仓库名称"
                                    clearable
                            />
                        </el-form-item>
                        <el-form-item label="仓库信息" prop="storagedetail">
                            <el-input
                                    style="width: 70%"
                                    v-model="storage.storagedetail"
                                    type="textarea"
                                    :rows="6"
                                    placeholder="请输入仓库详细信息"
                                    clearable
                            />
                        </el-form-item>
                    </el-form>

                </div>
            </el-card>
        </el-col>
    </el-row>
</template>

<script>
    import {storageDetailList, storageDetailUpdate} from "../../utils/actions";

    export default {
        data() {
            return {
                storageArr: []
            }
        },
        methods: {
            handleSave(storage) {
                if(!storage.storagename || !storage.storagedetail) {
                    this.msgError('参数不能为空！');
                    return;
                }
                storageDetailUpdate(storage).then(response => {
                    this.msgSuccess('保存成功！');
                }).catch(e => {
                    this.msgError('保存失败！');
                })

            }
        },
        created() {
            storageDetailList({}).then(response => {
                this.storageArr = response.data.list;
            }).catch(e => {
                this.msgError('数据读取失败！');
            })
        }
    }
</script>

<style scoped lang="scss">

</style>


