import Vue from 'vue'
import Router from 'vue-router'

import Index from "../pages/Index";
import Login from "../pages/Login"
import SettingIndex from "../pages/SettingIndex";

import Shelf3D from "../pages/storage/Shelf3D";
import StorageMap from "../pages/storage/StorageMap";
import ShelfSetting from "../pages/shelf/ShelfSetting";
import StorageSetting from "../pages/shelf/StorageSetting";

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            redirect: '/storageMap',
        },{
            path: '/login',
            name: 'Login',
            component: Login,
        },{
            path: '/index',
            name: 'Index',
            component: Index,
            children: [{
                path: '/shelf3D',
                name: 'Shelf3D',
                component: Shelf3D
            },{
                path: '/storageMap',
                name: 'StorageMap',
                component: StorageMap
            }]
        },{
            path: '/settingIndex',
            name: 'SettingIndex',
            component: SettingIndex,
            children: [{
                path: '/shelfSetting',
                name: 'ShelfSetting',
                component: ShelfSetting
            },{
                path: '/storageSetting',
                name: 'StorageSetting',
                component: StorageSetting
            },]
        },
    ]
})



