/*
*  待检区  模型
* */
import * as THREE from 'three'
import {PLANE_LENGTH} from "./config";
let STORAGE_WIDTH, STORAGE_HEIGHT, group;
export function stayModule(scene, STORAGE_WIDTH, STORAGE_HEIGHT, font, stayArr) {
    group = new THREE.Group();
    stayArr.forEach(stay => {
        addPlan(stay);
        addText(font, stay);
    })
    group.position.set(-STORAGE_WIDTH/2, 0, -STORAGE_HEIGHT/2);
    scene.add(group);
}

function addPlan(stay) {
    new THREE.TextureLoader().load("./static/image/plane.png", function (texture) {
        texture.wrapS = texture.wrapT = THREE.RepeatWrapping;
        let geo = new THREE.PlaneGeometry(stay.planeWidth,stay.planeHeight);
        let mat = new THREE.MeshBasicMaterial( { map: texture, side: THREE.DoubleSide, color: '#fff' } );
        let mesh = new THREE.Mesh(geo, mat);
        mesh.position.set(stay.planePosX, 2, stay.planePosZ);
        mesh.rotateX(-Math.PI / 2);
        group.add(mesh);
    })
}

function addText(font, stay) {
    let shapes = font.generateShapes( stay.text, 20);
    let geometry = new THREE.ShapeBufferGeometry( shapes );
    let material = new THREE.MeshBasicMaterial({color: "#ff0000"});
    let text = new THREE.Mesh( geometry, material );
    text.rotateX(-Math.PI/2);
    text.rotateZ(stay.textAngle);
    text.position.set(stay.textPosX, 2, stay.textPosZ);
    group.add(text);
}



