import request from './request'

/*****************************************************/
// 根据仓库号 获取货架信息
export function shelfList(params) {
    return request({
        url: 'storage/list',
        method: 'post',
        params: params
    })
}

//获取 物料 数组
export function materialList(params) {
    return request({
        url: 'material/list',
        method: 'post',
        params: params
    })
}

//货架分页列表
export function shelfListPagin(params) {
    return request({
        url: 'shelf/list',
        method: 'post',
        params: params
    })
}

//货架修改
export function shelfUpdate(params) {
    return request({
        url: 'shelf/update',
        method: 'post',
        params: params
    })
}

//仓库详细信息  列表
export function storageDetailList(params) {
    return request({
        url: 'storagedetail/list',
        method: 'post',
        params: params
    })
}

//仓库详细信息  修改
export function storageDetailUpdate(params) {
    return request({
        url: 'storagedetail/update',
        method: 'post',
        params: params
    })
}



