// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Print from 'vue-print-nb'

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);
//项目中所有拥有 size 属性的组件的默认尺寸均为 'small'，弹框的初始 z-index 为 3000。
Vue.prototype.$ELEMENT = { size: 'small', zIndex: 3000 };

import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
Vue.use(VueAwesomeSwiper, /* { default options with global component } */)


Vue.config.productionTip = false;
Vue.use(Print);

Vue.prototype.StatusColor = 'rgb(253 253 233)';
// Vue.prototype.selectDictLabel = selectDictLabel;
// Vue.prototype.getFormatDate = getFormatDate;

Vue.prototype.msgSuccess = function (msg='操作成功') {
    this.$message({ showClose: true, message: msg, type: "success" });
}
Vue.prototype.msgError = function (msg='操作失败') {
    this.$message({ showClose: true, message: msg, type: "error" });
}


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



