/*
*   货架  模型
*/
import * as THREE from 'three'
import {PLANE_GROUND,RULE_PLANE_WIDTH, PUSH_PLANE_WIDTH, PLANE_LENGTH, PLANE_HEIGHT,
    HOLDER_WIDTH, HOLDER_HEIGHT, BOX_SIZE} from "./config";

import {storageAreaMap} from "../../utils/comObject";
import {wallModule} from "./wallModule";
import {stayModule} from "./stayModule";
import {materialModule} from "./materialModule";

let STORAGE_WIDTH, STORAGE_HEIGHT, //仓库长宽
    PLANE_WIDTH,     //仓位宽度
    fontSize,       //字体大小
    isPositive,     //仓位排列顺序  是否 正序
    storageMap,     //仓库信息
    scene,
    font;
export function initShelf(initScene, storageId, shelfDataMap, materialDataMap) {
    scene = initScene;
    storageMap = storageAreaMap.get(storageId);
    STORAGE_WIDTH = storageMap.storageWidth;
    STORAGE_HEIGHT = storageMap.storageHeight;
    addShelf(storageId, shelfDataMap, materialDataMap);
}
//添加货架
function addShelf(storageId, shelfDataMap, materialDataMap) {
    new THREE.FontLoader().load('./static/js/FZYaoTi_Regular.json',function(fontLoader){
        font = fontLoader;
        const {shelfArr,doorPosX, stayArr} = storageMap;
        // 整排货架  循环
        shelfArr.map(shelf => {
            let overallGroup = new THREE.Group();
            let {begin, end, posX, posZ, interval, angle, positive} = shelf;
            let beforeRow = 0;
            let beforePosX = 0;
            isPositive = positive;
            //单个货架 循环
            for(let i=begin; i<=end; i++) {
                const shelfbin = storageId + (i<10?'-0':'-') + i;
                let shelfData = shelfDataMap.get(shelfbin);
                let {shelftype, shelfrow} = shelfData;
                if(i === begin) beforeRow = shelfrow;
                if((i-begin)%interval === 0) {
                    addHolder(overallGroup, beforeRow, beforePosX, 0);
                    addHolder(overallGroup, beforeRow, beforePosX, PLANE_LENGTH);
                }
                if(shelftype === 1) {
                    PLANE_WIDTH = RULE_PLANE_WIDTH;
                    fontSize = 5;
                } else {
                    PLANE_WIDTH = PUSH_PLANE_WIDTH;
                    fontSize = 3;
                }
                beforePosX = addRule(shelfbin, shelfData, beforeRow, overallGroup, beforePosX);
                beforeRow = shelfrow;
            }
            addHolder(overallGroup, beforeRow, beforePosX, 0);
            addHolder(overallGroup, beforeRow, beforePosX, PLANE_LENGTH);

            // 初始位置
            let overallPosX = -STORAGE_WIDTH / 2 + posX;
            let overallPosZ = -STORAGE_HEIGHT / 2 + posZ;
            overallGroup.position.set(overallPosX, 0, overallPosZ);
            overallGroup.rotateY(angle);
            scene.add(overallGroup);
        })
        //  墙
        wallModule(scene, STORAGE_WIDTH, STORAGE_HEIGHT, doorPosX);
        //  待检区
        stayModule(scene, STORAGE_WIDTH, STORAGE_HEIGHT, font, stayArr);
        //  物料
        materialModule(scene, shelfDataMap, materialDataMap);
    });
}

//添加  常规货架 整体   |-
function addRule(shelfbin, shelfData, beforeRow, overallGroup, beforePosX) {
    let group = new THREE.Group();
    let {shelfrow,shelfcol} = shelfData;
    for(let i=1; i<=shelfrow; i++) {
        let planeHeight = PLANE_GROUND + HOLDER_HEIGHT * (i-1);
        let planeGroup = addPlane(shelfbin, i, shelfcol);
        planeGroup.position.set(0, planeHeight, 0);
        group.add(planeGroup);
    }
    group.position.set(beforePosX, 0, 0);
    group.name = shelfbin;
    beforePosX += PLANE_WIDTH * shelfcol;

    overallGroup.add(group);
    return beforePosX;
}

// 竖 支架
const holderMatBase = new THREE.MeshPhongMaterial({color:'#0050b3'});
function addHolder(group, shelfrow, posX = 0, posZ = 0) {
    const holderMat = holderMatBase.clone();
    //支架高度
    const holderHeight = HOLDER_HEIGHT * shelfrow;
    const holderGeo = new THREE.BoxGeometry(HOLDER_WIDTH, holderHeight, HOLDER_WIDTH);
    const holderMesh = new THREE.Mesh(holderGeo, holderMat);
    holderMesh.position.set(posX, holderHeight/2, posZ);
    group.add(holderMesh);
}

// 横  面板
const planeMatBase = new THREE.MeshPhongMaterial({color:'#096dd9'});
function addPlane(shelfbin, rowIndex, shelfcol) {
    let planeGroup = new THREE.Group();
    const planeMat = planeMatBase.clone();
    //面板宽度
    const planeWidth = PLANE_WIDTH * shelfcol;
    const planeGeo = new THREE.BoxGeometry(planeWidth, PLANE_HEIGHT, PLANE_LENGTH);
    const planeMesh = new THREE.Mesh(planeGeo, planeMat);
    planeMesh.position.set(planeWidth/2, PLANE_HEIGHT/2, PLANE_LENGTH/2);
    planeGroup.add(planeMesh);

    //添加文字
    for(let i=1; i<=shelfcol; i++) {
        let no = (rowIndex-1) * shelfcol + i;
        let storageNo = shelfbin + "-0" + (no>10?'':'0') + no;
        let posX = (i-1) * PLANE_WIDTH;
        addArea(storageNo, planeGroup, posX);
    }
    return planeGroup;
}


//  添加货架文字
function addArea(name, planeGroup, posX) {
    let shapes = font.generateShapes( name, fontSize);
    let geometry = new THREE.ShapeBufferGeometry( shapes );
    let material = new THREE.MeshBasicMaterial({color:'#fff'});
    let text = new THREE.Mesh( geometry, material );
    let textPosZ = PLANE_LENGTH + 2;
    posX = posX + 6;
    if(!isPositive) {
        textPosZ = -2;
        text.rotateY(-Math.PI);
        posX = posX + PLANE_WIDTH - 10;
    }
    text.position.set(posX, 2, textPosZ);
    text.name = 'font-' + name;
    planeGroup.add(text);
}


