<template>
    <div class="box-card">
        <div class="storage-card-header">
            <span class="title">{{storageDetail.storagename}}</span>
            <i class="storage-back" >
                <span @click="handleDetail">物料</span>
                <span> | </span>
                <span @click="setting">返回</span>
            </i>
        </div>
        <div class="storage-card-body">
            <p v-for="(item, index) in detail" :key="index">{{item}}</p>
        </div>
    </div>
</template>

<script>
    import {storageDetailList} from "../utils/actions";

    export default {
        data() {
            return {
                storageDetail: {},
                detail: [],
            }
        },
        props: {
            storageId: String
        },
        methods: {
            //返回首页
            setting() {
                this.$router.push('/');
            },
            handleDetail() {
                this.$emit('materialArrShow');
            }
        },
        created() {
            storageDetailList({storageId:this.storageId}).then(response => {
                let storageArr = response.data.list;
                if(storageArr.length >= 0) this.storageDetail = storageArr[0];
                this.detail = this.storageDetail.storagedetail.split('\n');
            }).catch(e => {

            })

        }
    }
</script>

<style scoped lang="scss">
    .box-card {
        position: fixed;
        top: 30px;
        right: 30px;
        color: #613400;
        background-color: #fff1b8;
        border-radius: 5px;
        box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
        .storage-card-header {
            padding: 14px 40px;
            border-bottom: 1px solid #EBEEF5;
            font-size: 16px;
            .title {
                margin-right: 20px;
                font-weight: bolder;
            }
            .storage-back {
                color: #1989fa;
                position: absolute;
                right: 20px;
                top: 15px;
                font-size: 14px;
                &:hover {
                     cursor: pointer;
                 }
            }
        }
        .storage-card-body {
            line-height: 30px;
            margin: 10px 25px;
        }
    }
</style>


