<template>
    <div class="storage-map">
        <img class="img-map" :src="selectArea" usemap="#img-map" alt="" style="border-style:none" @mouseover="mouseoverArea"/>
        <map id="img-map" name="img-map">
            <area v-for="item in mapData" :key="item.id" shape="poly" :coords="item.coords" @mouseover.prevent="hoverArea(item.id)" @click="clickArea(item.title)" />
        </map>
        <!--仓库设置-->
        <el-tooltip class="item" effect="dark" content="仓库设置" placement="left">
            <i class="el-icon-s-tools" @click="setting"></i>
        </el-tooltip>

        <div class="dialog-warp">
            <div class="dialog-1 dialog-border" ref="dialog1">45/47/48</div>
            <div class="dialog-2 dialog-border" ref="dialog2">B5</div>
            <div class="dialog-3 dialog-border" ref="dialog3">46</div>
            <div class="dialog-4 dialog-border" ref="dialog4">41/43</div>
        </div>
        <el-dialog title="仓库选择" :visible.sync="visible45" width="544px">
            <div class="dialog-storage dialog-storage-45">
                <div class="storage-item" @click="pushStorage('45')">45</div>
                <div class="storage-item" @click="pushStorage('47')">47</div>
                <div class="storage-item" @click="pushStorage('48')">48</div>
            </div>
        </el-dialog>
        <el-dialog title="仓库选择" :visible.sync="visible41" width="444px">
            <div class="dialog-storage dialog-storage-41">
                <div class="storage-item storage-43" @click="pushStorage('43')">43</div>
                <div class="storage-item storage-41" @click="pushStorage('41')">41</div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {mapData} from "../../utils/comObject";
    import storage0 from "../../assets/image/storage.jpg";
    let imgWidth = 25;
    let imgHeight = 14;

    export default {
        data() {
            return {
                mapData: [],
                imgArr: [],
                selectArea: storage0,
                visible45: false,       //45、47、48仓库选择
                visible41: false,       //41、43仓库选择
            }
        },
        methods: {
            //鼠标进入图片
            hoverArea(mapId) {
                this.selectArea = this.imaArr[mapId] ;
            },
            //地图点击
            clickArea(storageId) {
                if(storageId === '45') {
                    this.visible45 = true;
                    return;
                } else if(storageId === '41') {
                    this.visible41 = true;
                    return;
                } else if(storageId === 'B5') {
                    return;
                }
                this.pushStorage(storageId);
            },
            //跳转
            pushStorage(storageId) {
                this.$router.push({path: "/shelf3D", query: {storageId}});
            },
            //离开
            mouseoverArea() {
                this.selectArea = storage0;
            },
            //重置地图位置
            resetMapData() {
                let winWidth = window.innerWidth;
                let winHeight = window.innerHeight;

                let dataArr = [];
                mapData.forEach(item => {
                    let coordsStr  = '';
                    let data = Object.create(item);
                    data.coordsArr.forEach(coords => {
                        let posX = (coords[0] / imgWidth * winWidth).toFixed(2);
                        let posY = (coords[1] / imgHeight * winHeight).toFixed(2);
                        coordsStr += posX + ',' + posY + ',';
                    })
                    data.coords = coordsStr.substring(0, coordsStr.length-1);
                    dataArr.push(data);
                });
                this.mapData = dataArr;
            },

            //重置标签位置
            resetDialog() {
                let winWidth = window.innerWidth;
                let winHeight = window.innerHeight;
                let pos = [[8.8, 3.6],[7.03, 5.1],[14, 10.4],[15.1, 8.6]];
                for(let i=0; i< pos.length; i++) {
                    let left = 20;
                    if(i == 1 || i==2) {
                        left = -70;
                    }
                    this.$refs["dialog"+(i+1)].style.left = (pos[i][0] / imgWidth * winWidth + left).toFixed(2) + "px";
                    this.$refs["dialog"+(i+1)].style.top = (pos[i][1] / imgHeight * winHeight - 10).toFixed(2) + "px";
                }

            },

            //仓库设置
            setting() {
                this.$router.push('/login');
            }
        },
        mounted() {
            this.resetDialog();
            this.$nextTick(() => {
                this.imaArr = [require("../../assets/image/storage1.jpg"),
                    require("../../assets/image/storage2.jpg"),
                    require("../../assets/image/storage3.jpg"),
                    require("../../assets/image/storage4.jpg"),
                ]
            });
            window.onresize = () => {
                return (() => {
                    this.resetMapData();
                    this.resetDialog();
                })();
            };
        },
        created() {
            this.resetMapData();
        }
    }
</script>

<style scoped lang="scss">
    @mixin dialog-left {
        content: "";
        position: absolute;
        top: 10px;
        left: -20px;
        z-index: 10;
        border-top: 6px solid transparent;
        border-right: 20px solid #29a3de;
        border-bottom: 6px solid transparent;
    }
    @mixin dialog-right {
        content: "";
        position: absolute;
        top: 10px;
        left: 50px;
        z-index: 10;
        border-top: 6px solid transparent;
        border-left: 20px solid #29a3de;
        border-bottom: 6px solid transparent;
    }
    .storage-map {
        width: 100%;
        height: 100%;
        overflow: hidden;
        .img-map {
            width: 100vw;
            height: 99vh;
        }
        .dialog-warp {
            position: relative;
            font-size: 16px;
            color: white;
            text-align: center;
            line-height: 40px;
            .dialog-border {
                background-color: #29a3de;
                border-radius: 5px;
                position: fixed;
                z-index: 10;
                height: 40px;
            }
            .dialog-1 {
                width: 100px;
                &:before {
                    @include dialog-left;
                }
            }
            .dialog-2 {
                width: 50px;
                top: 100px;
                left: 100px;
                &:before {
                    @include dialog-right;
                }
            }
            .dialog-3 {
                width: 50px;
                &:before {
                    @include dialog-right;
                }
            }
            .dialog-4 {
                width: 80px;
                &:before {
                    @include dialog-left;
                }
            }
        }

        //弹窗选择仓库
        .dialog-storage {
            border: 2px #409EFF dashed;
            display: flex;
            .storage-item {
                flex: 1;
                text-align: center;
                background-color: #e6f7ff;
                margin: 5px;
                font-size: 30px;
                line-height: 140px;
                font-weight: bolder;
                color: #002766;
                cursor: pointer;
                &:hover {
                    background-color: #bae7ff;
                }
            }
            .storage-43 {
                flex: 2;
                margin-right: 0;
            }
            .storage-41 {
                margin-left: 0;
                border-left: 2px dashed #409EFF;
            }
        }
        .dialog-storage-45 {
            width: 500px;
            height: 150px;
        }

        // 设置图标
        .el-icon-s-tools {
            color: #fff;
            font-size: 40px;
            position: absolute;
            top: 20px;
            right: 40px;
            z-index: 20;
            cursor: pointer;
        }
    }
</style>


