/*
*  墙、门  模型
* */
import * as THREE from 'three'
let STORAGE_WIDTH, STORAGE_HEIGHT, group;
export function wallModule(scene, storageWidth, storageHeight, doorPoxX) {
    group = new THREE.Group();
    STORAGE_WIDTH = storageWidth;
    STORAGE_HEIGHT = storageHeight;
    addDoor(doorPoxX);
    addWall();
    addFrontWall(doorPoxX);

    group.position.set(-storageWidth/2, 0, -storageHeight/2);
    scene.add(group);

}

const baseWallMaterial = new THREE.MeshPhongMaterial({color: 0xafc0ca});
//墙高度、厚度
let wallHeight = 100, wallDepth = 10;
function addWall() {
    let geometry = new THREE.BoxGeometry(STORAGE_HEIGHT, wallHeight, wallDepth);
    let leftWall = new THREE.Mesh(geometry, baseWallMaterial);
    let rightWall = leftWall.clone();
    let backGeometry = new THREE.BoxGeometry(STORAGE_WIDTH, wallHeight, wallDepth);
    let backWall = new THREE.Mesh(backGeometry, baseWallMaterial);

    leftWall.rotateY(Math.PI / 2);
    leftWall.position.set(0, wallHeight/2, STORAGE_HEIGHT/2);

    rightWall.rotateY(Math.PI / 2);
    rightWall.position.set(STORAGE_WIDTH, wallHeight/2, STORAGE_HEIGHT/2);

    backWall.position.set(STORAGE_WIDTH/2, wallHeight/2, 0);
    group.add(leftWall);
    group.add(rightWall);
    group.add(backWall);
}


function addFrontWall(doorPoxX) {
    let frontGroup = new THREE.Group();
    let wallWidth1 = doorPoxX;
    let geometry1 = new THREE.BoxGeometry(wallWidth1, wallHeight, wallDepth);
    let wall1 = new THREE.Mesh(geometry1, baseWallMaterial);
    wall1.position.x = wallWidth1 / 2;

    let wallWidth2 = 50;
    let geometry2 = new THREE.BoxGeometry(wallWidth2, wallHeight - 70, wallDepth);
    let wall2 = new THREE.Mesh(geometry2, baseWallMaterial);
    wall2.position.set(wallWidth1 + wallWidth2 / 2, 35, 0);

    let wallWidth3 = STORAGE_WIDTH - wallWidth1 - wallWidth2;
    let geometry3 = new THREE.BoxGeometry(wallWidth3, wallHeight, wallDepth);
    let wall3 = new THREE.Mesh(geometry3, baseWallMaterial);
    wall3.position.x = wallWidth1 + wallWidth2 + wallWidth3/2;
    frontGroup.add(wall1);
    frontGroup.add(wall2);
    frontGroup.add(wall3);

    frontGroup.position.set(0, wallHeight/2, STORAGE_HEIGHT);
    group.add(frontGroup);
}

function addDoor(doorPoxX) {
    new THREE.TextureLoader().load("./static/image/door.png", function (texture) {
        let doorGeo = new THREE.PlaneGeometry(50,50);
        let doorMat = new THREE.MeshBasicMaterial( { map: texture, side: THREE.DoubleSide, color: '#fff' } );
        let doorMesh = new THREE.Mesh(doorGeo, doorMat);
        doorMesh.position.set(doorPoxX+25, 2, STORAGE_HEIGHT-25);
        doorMesh.rotateX(-Math.PI / 2);
        group.add(doorMesh);
    })
}



