function initMap(map, storageId, begin, end, row, col, type) {
    for(let i=begin; i<=end; i++) {
        let bin = storageId + (i<10?'-0':'-') + i;
        let data = {
            storageBin: bin,
            storageType: type,
            storageRow: row,
            storageCol: col
        }
        map.set(bin, data);
    }
}

