<template>
    <el-card ref="cardTable">
        <div slot="header" style="margin-bottom: 20px">
            <span style="color: #1989fa;font-weight: bolder">{{title}}</span>
            <el-form :model="queryParams"
                     ref="queryForm"
                     style="float: right; padding: 3px 0"
                     @submit.native.prevent
                     :inline="true">
                <el-form-item label="货架编号" prop="shelfBin">
                    <el-input
                            v-model="queryParams.shelfBin"
                            placeholder="请输入货架编号"
                            clearable
                            @keypress.native.enter="handleQuery"
                    />
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-table
                :data="tableData"
                v-loading="loading"
                style="width: 100%">
            <el-table-column prop="shelfbin" label="货架编号" />

            <el-table-column prop="shelftype" label="货架类型">
                <template slot-scope="scope">
                    <el-select v-model="scope.row.shelftype" clearable placeholder="请选择">
                        <el-option
                                v-for="item in shelfType"
                                :key="item.index"
                                :label="item.title"
                                :value="item.index">
                        </el-option>
                    </el-select>
                </template>
            </el-table-column>

            <el-table-column prop="shelfrow" label="货架行数">
                <template  slot-scope="scope">
                    <el-input-number v-model="scope.row.shelfrow" :min="1" :max="10" label="请输入货架行数"></el-input-number>
                </template>
            </el-table-column>

            <el-table-column prop="shelfcol" label="货架列数">
                <template  slot-scope="scope">
                    <el-input-number v-model="scope.row.shelfcol" :min="1" :max="10" label="请输入货架列数"></el-input-number>
                </template>
            </el-table-column>

            <el-table-column label="操作" align="center" class-name="small-padding fixed-width">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            type="text"
                            icon="el-icon-edit"
                            @click="handleUpdate(scope.row)"
                    >修改</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--分页-->
        <el-pagination
                style="display: flow; float: right"
                background
                layout="prev, pager, next"
                :current-page="queryParams.page"
                :total="total"
                :page-size="20"
                @current-change="currentChange"
        >
        </el-pagination>
    </el-card>
</template>

<script>
    import {shelfType} from '../../utils/dict';
    import {shelfListPagin, shelfUpdate} from "../../utils/actions";

    export default {
        data() {
            return {
                title: '货架管理',
                loading: false,
                tableData: [],
                //总页数
                total: 1,
                shelfType: [],
                unitGroup: [],
                // 查询参数
                queryParams: {
                    page: 1,
                    size: 20,
                    shelfBin: '41'
                },
            }
        },
        methods: {
            /** 查询列表 */
            getList() {
                this.loading = true;
                shelfListPagin(this.queryParams).then(response => {
                    this.tableData = response.data.list;
                    this.total = response.data.total;
                    this.loading = false;
                }).catch(e => {

                })
            },
            /** 搜索按钮操作 */
            handleQuery() {
                this.queryParams.page = 1;
                if(!this.queryParams.shelfBin) this.queryParams.shelfBin = '41';
                this.getList();
            },
            /** 重置按钮操作 */
            resetQuery() {
                this.$refs.queryForm.resetFields();
                this.handleQuery();
            },

            /* 修改按钮*/
            handleUpdate(row) {
                if(!row.shelftype || !row.shelfrow || !row.shelfcol) {
                    this.msgError('参数不能为空！')
                    return;
                }
                shelfUpdate(row).then(response => {
                    this.msgSuccess("修改成功");
                }).catch(e => {

                })
            },

            submitForm() {
                this.$refs["form"].validate(valid => {
                    if (valid) {
                        updateMaterials(this.form).then(response => {
                            this.msgSuccess();
                            this.getList();
                        });
                    }
                });
            },

            currentChange(pageNum) {
                this.queryParams.page = pageNum;
                this.getList();
            },
        },
        created() {
            let isLogin = sessionStorage.getItem("isLogin");
            if(!isLogin) {
                this.$router.push('/login');
            }
            this.getList();
            this.shelfType = shelfType;
        }
    }
</script>

<style scoped lang="scss">
    .handle {
        margin-bottom: 20px;
    }
    .el-pagination {
        margin: 20px 0;
    }

</style>



